/***/ (function(module, exports) {

	module.exports = {"data":{"post":{"html":"<p>It took over 8 weeks, but as of yesterday Frauke's <a href=\"http://en.wikipedia.org/wiki/Gmbh\">GmbH</a> is finally registered with the local court in Tostedt. (GmbH is short for <em>Gesellschaft mit beschränkter Haftung</em>, literally \"company with limited liability\", a form of incorporation common in Germany.)</p>\n<p>Theoretically the formation of a GmbH takes only a week, but the papers returned twice (or was it three times) by the court to our notary for minor errors. We had become extremely annoyed by the entire process, and finally yesterday Frauke personally went to the court yesterday to finalize the process. We absolutely needed the HRB number now in order to register and get plates for her company car, which is being delivered today.</p>\n<p>The lack of papers has also complicated our dealings with the bank and the Finanzamt (not only for our own taxes, but for VAT and withholding taxes from wages), and not least delayed the formal purchase contract for the restaurant. We finally be able to get all these affairs settled.</p>\n<p>A GmbH is a matter of public record, so you can go to <a href=\"http://www.handelsregister.de/\">http://www.handelsregister.de/</a>, <a href=\"http://www.handelsregister.de/rp_web/mask.do?Typ=n\">search</a> for Registernummer 200842 at Registergericht Tostedt, and see the details for yourself.</p>","fields":{"title":"Tostedt HRB 200842","date":"18 Jul 2007","url":"https://papascott.de/2007-07-18-tostedt-hrb-200842/"}}},"pathContext":{"slug":"/2007-07-18-tostedt-hrb-200842/","prev":{"fields":{"title":"Minus Five","date":"15 Jul 2007","slug":"/2007-07-15-minus-five/"}},"next":{"fields":{"title":"And After","date":"19 Jul 2007","slug":"/2007-07-19-and-after/"}}}}

/***/ })

